.header {
  position: fixed;
  width: 100vw;
  // max-width: 1720px;

  // height: 120px;
  // border: 1px solid red;
  padding: 0 100px;
  //   background: $page-bgn-color;
  z-index: 100;
  margin-top: 25px;
  // margin-right: auto;
}
.header__container {
  // padding: 0 100px;
  // margin: 0 100px;
  // border: 1px solid red;

  display: flex;
  margin: 0 auto;
  width: 100%;
  max-width: 1720px;
  // height: 120px;
  justify-content: space-between;
  align-items: center;
}
.header__nav-list {
  display: flex;
  gap: 54px;
  color: $color-white;
  //   margin: 0 auto;
  //   width: auto;
  //   height: 100px;
}

.header__nav-list-item {
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 19.25px;
  letter-spacing: 0.18px;

  &:hover {
    scale: (1.2);
  }
}

.header__logo {
  // background-color: grey;
  height: 120px;
}
.header__contacts {
  display: flex;
  gap: 25px;
  align-items: center;
}
.header__contacts-item {
  display: flex;
  gap: 8px;
  align-items: center;
  justify-content: center;
}

.phone-icon {
  fill: $color-text;
}

.header__contacts-item button {
  background: transparent;
  border: none;
}
.header__contacts-item img {
  fill: $color-white;
}

.header__contacts-item p {
  color: $color-text;
  font-family: Montserrat;
  font-size: 22px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 0.22px;
}

.header__button-link {
  display: inline-flex;
  padding: 3px 39px 3px 5px;
  align-items: center;
  gap: 13px;
  border-radius: 68px;
  border: 1px solid #fff;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(1.5px);
}

.header__button-link p {
  color: $color-white;
  text-align: center;
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 22px;
  letter-spacing: 0.18px;
}

.button-ellipse {
  display: flex;
  width: 38px;
  height: 38px;
  align-items: center;
  justify-content: center;
  border-radius: 68px;
  border: 1px solid var(--dark-gren, #095119);
  background: $color-white;
}

.language-switcher {
  display: flex;
}

.language-switcher button {
  background: transparent;
  border: none;
  color: $color-white;
  font-family: Montserrat;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  opacity: 0.6;
}
.language-switcher button.active {
  opacity: 1;
}

